/* General Styles */
body {
  margin: 0;
  font-family: 'Roboto', Arial, sans-serif;
  line-height: 1.6;
  position: relative;
  color: #e0e0e0;
  overflow-x: hidden;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(./MountKenya.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  filter: blur(3px);
  z-index: -1;
}

.about-content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 40px;
  flex-wrap: wrap;
}

.about-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 400px;
  padding: 20px;
  background: rgba(45, 45, 45, 0.8);
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  text-align: center;
}

.center-image {
  border-radius: 50px;
  transition: all 1s;
  max-width: 500px;
  height: auto;
  margin: 10px auto;
  display: block;
}

img:hover {
  transform: scale(1.1);
}

.about-photo {
  max-width: 250px;
  height: auto;
  margin: 10px auto;
  display: block;
}

/* Project Section */
.projects-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 40px;
  text-align: center;
}

.project-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 400px;
  padding: 20px;
  background: rgba(45, 45, 45, 0.9);
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  text-align: center;
}

.project-photo {
  width: 100%;
  max-width: 250px;
  height: auto;
  border-radius: 10px;
}

.projects-title {
  text-align: center;
  margin: 20px 0;
  font-size: 2.5rem;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  color: gold;
}

p {
  font-size: 1.1em;
  color: #b0b0b0;
  font-weight: bold;
  text-align: center;
}

a {
  text-align: center;
  display: block;
}

a:hover {
  text-decoration: underline;
}

.reduced-size {
  max-width: 250px;
  height: auto;
  margin: 10px auto;
  display: block;
}

.retry-button {
  padding: 10px 20px;
  font-size: 1em;
  background-color: #1e90ff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.retry-button:hover {
  background-color: #4682b4;
}

/* Orbit Animation */
@keyframes orbit {
  0% {
    transform: rotate(0deg) translateX(150px);
  }
  100% {
    transform: rotate(360deg) translateX(150px);
  }
}

.orbit-image:nth-child(1) {
  animation-delay: 0s;
}

.orbit-image:nth-child(2) {
  animation-delay: 2s;
}

.orbit-image:nth-child(3) {
  animation-delay: 4s;
}

.orbit-image:nth-child(4) {
  animation-delay: 6s;
}

.orbit-image:nth-child(5) {
  animation-delay: 8s;
}

.nasa-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  margin: 0 auto;
  max-width: 1000px;
  padding: 20px;
  min-width: 300px;
  background: rgba(45, 45, 45, 0.8);
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
}

.nasa-media-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.nasa-photo {
  max-width: 100%;
  height: auto;
}

.nasa-video {
  width: 100%;
  max-height: 500px;
}

.nasa-description {
  max-width: 800px;
  padding: 0 15px;
  margin-top: 20px;
}

h1, h2, h3 {
  font-size: 50px;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  color: rgb(255, 217, 0);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 4);
}

.welcome-subheading {
  font-size: 50px;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  color: rgb(255, 217, 0);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 4);
}

.typewriter {
  font-size: 2.5rem;
  font-family: 'Poppins', sans-serif;
  color: gold;
  white-space: pre;
  overflow: hidden;
  width: fit-content;
  margin: 100px auto 100px;
  animation: blink-caret 0.6s step-end infinite;
}

@keyframes gradient-move {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Blinking Caret Animation */
@keyframes blink-caret {
  from, to {
    border-color: transparent;
  }
  50% {
    border-color: #38bdf8;
  }
}

/* Navbar */
.navbar {
  position: absolute;
  top: 20px;
  width: 100%;
  text-align: center;
  z-index: 10;
}

.navbar-menu {
  list-style: none;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  padding: 0;
  margin: 20;
  display: flex;
  justify-content: center;
  gap: 30px;
  animation: scale-in 2s ease-out;
}

.navbar-menu a {
  color: gold; /* Set the navbar text color to gold */
  text-decoration: none;
  font-size: 1.2rem;
  transition: color 0.3s ease-in-out;
}

.section {
  text-align: center;
  padding: 30px 20px;
}

/* Global Header */
.global-header {
  text-align: center;
  position: relative;
  overflow: hidden;
}

/* About Page */
.about {
  text-align: center;
  padding: 20px;
}

.projects-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

/* Contact Page */
.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 50px 20px;
}

.contact-title {
  font-size: 2.5rem;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  color: gold;
  margin-bottom: 20px;
}

.contact-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  max-width: 600px;
  background: rgba(45, 45, 45, 0.9);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
}

.contact-box {
  text-align: center;
  font-size: 1.2em;
}

.contact-link {
  color: #1e90ff;
  font-weight: bold;
  text-decoration: none;
}

.contact-link:hover {
  text-decoration: underline;
}

/* Social Media Icons */
.social-links {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.social-icon {
  width: 40px;
  height: 40px;
  transition: transform 0.3s ease-in-out;
}

.social-icon:hover {
  transform: scale(1.2);
}

/* Contact Form */
.contact-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  max-width: 400px;
}

.form-input,
.form-textarea {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  border: 2px solid #1e90ff;
  border-radius: 5px;
  background: #333;
  color: white;
}

.form-textarea {
  height: 100px;
  resize: none;
}

.contact-button {
  padding: 10px;
  font-size: 1.1em;
  background-color: #1e90ff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

.contact-button:hover {
  background-color: #4682b4;
}

/* Floating Pictures Section */
/* The container spans the full viewport and allows the pics to drift */
/* Floating Pictures Container */
.floating-pics-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* Allow interactions with underlying content */
  z-index: 50; /* Adjust as needed */
}

/* Each floating pic is absolutely positioned and animated */
.floating-pic {
  position: absolute;
  width: 120px;  /* Adjust the size as needed */
  height: auto;
  cursor: pointer;
  pointer-events: auto; /* Enable hover interactions */
  animation: lazyDrift 30s ease-in-out infinite;
}

/* Distribute initial positions using nth-child selectors */
.floating-pic:nth-child(1) {
  top: 10%;
  left: 10%;
}
.floating-pic:nth-child(2) {
  top: 20%;
  left: 70%;
}
.floating-pic:nth-child(3) {
  top: 40%;
  left: 30%;
}
.floating-pic:nth-child(4) {
  top: 60%;
  left: 80%;
}
.floating-pic:nth-child(5) {
  top: 80%;
  left: 20%;
}

/* Lazy drifting: the images only shift within a bounded area */
@keyframes lazyDrift {
  0%   { transform: translate(0, 0); }
  25%  { transform: translate(5vw, 5vh); }
  50%  { transform: translate(-5vw, 5vh); }
  75%  { transform: translate(-5vw, -5vh); }
  100% { transform: translate(0, 0); }
}

/* On hover, the image accelerates to a new nearby position */
.floating-pic:hover {
  animation: fastRun 1s ease-out forwards;
}

@keyframes fastRun {
  from { transform: none; }
  to   { transform: translate(10vw, -10vh); }
}

